  background: $purple;\n  border: none;\n  border-bottom: .3rem solid darken($purple, 10%);\n  color: $white;\n  font-weight: 500;\n  padding: $small-size;\n\n  &__link {\n    background: none;\n    border: none;\n    color: $off-white;\n    padding: 0;\n  }\n}\n\n@media (min-width: $desktop-breakpoint) {\n  .big-button {\n    margin-bottom: $extra-size;\n  }\n}\n",".container {\n  display: flex;\n  justify-content: center;\n  flex-direction: column;\n  align-items: center;\n  padding: 0 5%;\n}\n\n.full-width {\n  display: flex;\n  width: 100%;\n  flex-direction: column;\n}\n\n.widget {\n  background: $light-blue;\n  margin-bottom: $base-size;\n}\n\n.widget-header {\n  background: $blue;\n  display: flex;\n  justify-content: space-between;\n  padding: $base-size;\n  color: $off-white;\n  font-size: $base-size;\n\n  &__title {\n    margin: 0;\n  }\n}\n\n.widget-content {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  color: $off-white;\n\n  span {\n    border-bottom: 1px solid lighten($light-blue, 10%);\n    padding: $big-size 0;\n    width: 100%;\n    justify-content: center;\n    display: flex;\n  }\n}\n\n@media (min-width: $desktop-breakpoint) {\n  .container {\n    padding: 0 20%;\n  }\n}\n",".header {\n  background: $off-black;\n  color: $white;\n  margin-bottom: $base-size;\n  padding: $base-size 0;\n\n  &__title {\n    font-size: $big-size;\n    margin: 0;\n  }\n\n  &__subtitle {\n    color: $off-white;\n    font-size: $base-size;\n    font-weight: 500;\n    margin: 0;\n  }\n}\n\n@media (min-width: $desktop-breakpoint) {\n  .header {\n    margin-bottom: $extra-size;\n  }\n}\n",".ReactModalPortal {\n  > div {\n    opacity: 0;\n  }\n\n  .ReactModal {\n    &__Overlay {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n      transition: opacity 200ms ease-in-out;\n\n      &--after-open {\n        opacity: 1;\n      }\n\n      &--before-close {\n        opacity: 0;\n      }\n    }\n\n    &__Content {\n      display: flex;\n      flex-direction: column;\n      justify-content: center;\n    }\n  }\n}\n\n.modal {\n  background: $light-blue;\n  color: $white;\n  max-width: 80%;\n  outline: none;\n  padding: $big-size;\n\n  &__title {\n    margin: 0 0 $big-size 0;\n    font-size: 2.5rem;\n  }\n\n  &__body {\n    font-size: 2rem;\n    font-weight: 300;\n    text-align: center;\n    margin: 0 0 $big-size 0;\n    word-break: break-all;\n  }\n}\n\n@media (min-width: $desktop-breakpoint) {\n  .modal {\n    max-width: 50%;\n  }\n}\n",".option {\n  display: flex;\n  justify-content: space-between;\n  margin: 0;\n  padding: $big-size $base-size $big-size $big-size;\n  border-bottom: 1px solid lighten($light-blue, 10%);\n\n  span {\n    border: none;\n    display: flex;\n    justify-content: flex-start;\n    padding: 0;\n  }\n\n  &__text {\n    color: $white;\n    font-weight: 500;\n    font-size: 2rem;\n    margin: 0;\n    word-break: break-all;\n    margin-right: $base-size;\n  }\n}\n",".checkbox-container {\n  color: $off-white;\n  display: block;\n  position: relative;\n  padding-left: 30px;\n  margin-bottom: 12px;\n  cursor: pointer;\n  font-weight: 500;\n  font-size: $base-size;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  -ms-user-select: none;\n  user-select: none;\n\n  input {\n    position: absolute;\n    opacity: 0;\n    cursor: pointer;\n    height: 0;\n    width: 0;\n    &:checked {\n      ~ {\n        .checkmark {\n          background-color: darken($purple, 10%);\n          &:after {\n            display: block;\n          }\n        }\n      }\n    }\n  }\n  &:hover {\n    input {\n      ~ {\n        .checkmark {\n          background-color: $purple;\n        }\n      }\n    }\n  }\n  .checkmark {\n    &:after {\n      left: 7px;\n      top: 2px;\n      width: 5px;\n      height: 10px;\n      border: solid white;\n      border-width: 0 3px 3px 0;\n      -webkit-transform: rotate(45deg);\n      -ms-transform: rotate(45deg);\n      transform: rotate(45deg);\n    }\n  }\n}\n.checkmark {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 20px;\n  width: 20px;\n  background-color: #eee;\n  &:after {\n    content: \"\";\n    position: absolute;\n    display: none;\n  }\n}\n"],"sourceRoot":""}]);
